
.slide-right-enter-active,
  .slide-right-leave-active,
  .slide-left-enter-active,
  .slide-left-leave-active {
      will-change: transform;
      -webkit-transition: all .3s;
      transition: all .3s;
      position: absolute;
      width:100%;
      left:0;
}
.slide-right-enter {
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      opacity: 0;
}
.slide-right-leave-active {
      -webkit-transform: translateX(50%);
              transform: translateX(50%);
      opacity: 1;
}
.slide-left-enter {
      -webkit-transform: translateX(50%);
              transform: translateX(50%);
      opacity: 1;
}
.slide-left-leave-active {
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      opacity: 0;
}

